import { createRouter, createWebHashHistory } from 'vue-router'

const routes = [
  {
    path: "/",
    name: "qr_content",
    component: () => import('../components/qr_content.vue')
  },
  {
    path: "/wifi_content",
    name: "wifi_content",
    component: () => import('../components/wifi_content.vue')
  },
  {
    path: "/card_content",
    name: "card_content",
    component: () => import('../components/card_content.vue')
  },
  /*{
    path: "/wifi_content",
    name: "wifi_content",
    component: () => import('../components/wifi_content.vue')
  }*/
]
const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
