import { createStore } from 'vuex'

export default createStore({
  state: {
    RequestUrl:"https://api.qrserver.com/v1/create-qr-code/?size=150%C3%97150&data=",
    SendingAddress:""
  },
  getters: {
  },
  actions: {
    upSendingAddress(context,value){
      context.commit('UPSENDINGADDRESS',value);
    },
    upRequestUrl(context,value){
      context.commit('UPREQUSETURL',value);
    }
  },
  mutations: {
    UPSENDINGADDRESS(state,value){
      state.SendingAddress=value;
    },
    UPREQUSETURL(state,value){
      state.RequestUrl=value;
    }
  },
  modules: {
  }
})
