import { ref, getCurrentInstance, computed, watch } from "vue";
import { useStore } from "vuex";
export default {
  name: "wifi_code",
  props: {
    msg: String
  },

  setup() {
    const store = useStore();
    let loadShow = ref(false);
    let SendingAddress = ref(""); //用计算属性取到vuex中的值

    const getShowTask = computed(() => {
      return store.state.SendingAddress;
    }); //监视vux中发送地址的变化

    watch(getShowTask, (newVal, oldVal) => {
      loadShow.value = true;
      SendingAddress.value = newVal;
      console.log("SendingAddress变化了", SendingAddress.value);
    });
    const menuShow = ref(false);
    const activeNames = ref(["1"]);

    const showPopup = () => {
      menuShow.value = true;
    };

    const popupShow = () => {
      menuShow.value = false;
    }; //这里的proxy代替this


    const {
      proxy
    } = getCurrentInstance(); //是否展示气泡弹出层

    const showPopover = ref(false); // 通过 actions 属性来定义菜单选项

    const actions = [{
      text: "qrserver",
      path: "https://api.qrserver.com/v1/create-qr-code/?size=150%C3%97150&data="
    }, {
      text: "qun.qq",
      path: "https://qun.qq.com/qrcode/index?data="
    }]; //选中气泡选项事件

    const onSelect = action => {
      //封装的Toast轻提示
      toast(action.text, "top"); // 触发vuex中的actions，用于异步修改state的信息

      store.dispatch("upRequestUrl", action.path);
    }; //保存二维码


    function qrCodeDown() {
      if (SendingAddress.value) {
        imgDownload(SendingAddress.value, "qr_code");
      } else {
        toast("还未生成二维码哦", "middle");
      }
    }

    const menu = () => {
      showPopup();
    }; //下载二维码


    function imgDownload(url, name) {
      //创建一个a标签
      const a_b = document.createElement("a"); // 这里是将url转成blob地址，将下载地址借助Blob转换成二进制，然后，作为a标签的href属性，配合download属性，实现下载

      fetch(url).then(res => res.blob()).then(blob => {
        //设置href指向
        a_b.href = URL.createObjectURL(blob); //设置图片下载名字

        a_b.download = name;
        document.body.appendChild(a_b); //点击a标签

        a_b.click();
      });
    } //封装轻提示


    const toast = (message, position) => {
      proxy.$toast({
        message: message,
        position: position
      });
    };

    return {
      SendingAddress,
      actions,
      showPopover,
      onSelect,
      loadShow,
      qrCodeDown,
      toast,
      menuShow,
      showPopup,
      menu,
      activeNames,
      popupShow
    };
  }

};