import { createApp } from 'vue'
import App from './App.vue'
import store from './store'
import router from './router'
import { Button,Cell, CellGroup,NavBar,Image as VanImage,Toast,Popover,Popup,Collapse, CollapseItem,SidebarItem,NoticeBar, Form, Field} from 'vant'
//Toast函数组件需要手动引入css样式
import 'vant/es/toast/style';
const app = createApp(App).use(router).use(store);
app.use(Button,Cell,CellGroup,NavBar,VanImage,Popover,Popup,SidebarItem,NoticeBar,Collapse, CollapseItem, Form, Field);
app.config.globalProperties.$toast = Toast;
app.mount("#app");
